<script>
    import {mapActions} from 'vuex';

    export default {
        name: 'app',
        props: ['data'],
        updated() {
            $('body').off('click').on('click', "[data-action='fixedpanel-toggle']", (e) => {
                let panel = $('#fixed_panel')
                let backdrop = $('#backdrop')
                let btn = $('#sidebar_toggle_btn span')

                    this
            .
                $snotify.info("Test", "Sidebar toggled", {})

                if ($('body').hasClass('show-sidebar')) {
                    $('body').removeClass('show-sidebar')
                    $(btn).removeClass('fa-times').addClass('fa-cog')
                }
                else {
                    $('body').addClass('show-sidebar')
                    $(btn).removeClass('fa-cog').addClass('fa-times')
                }
            })
        },
        computed: {
            disableAnimations() {
                return this.$store.state.network.account.settings.client.animations;
            }
        },
        methods: mapActions(['loadSettings']),
        mounted() {
            this.loadSettings();
        },
        watch: {
            $route(to, from) {
                $('body').removeClass('show-sidebar')
                $('[data-action="fixedpanel-toggle"] span').removeClass('fa-times').addClass('fa-cog')
            }
        }
    }
</script>

